<template>
  <div>
    <!-- <div class="block"></div> -->
    <!---->
    <div class="cishu">{{ this.cishu }}</div>
    <div id="box">
      <div id="zhizhen"></div>
    </div>
    <div class="jiangpin">
      <ul>
        <li v-for="jiangpin in jiangpins">
          {{ jiangpin.prize }} - {{ jiangpin.name }}
        </li>
      </ul>
    </div>
    <router-link to="/wodejiangpin">
      <mt-button class="history" type="primary" size="small"></mt-button>
    </router-link>
  </div>
</template>
<script type="text/javascript">
import { Toast } from 'mint-ui'
import axios from 'axios'
import store from '@/vuex/store'

export default {
  name: 'choujiang',
  data() {
    return {
      cishu: 0,
      jiangpins: [],
      aword: 0,
    }
  },
  created() {
    // this.cishu = 3
    axios.get(`http://shine.tunnel.qydev.com/user/member/${this.$store.state.openId}`)
      .then(response => {
        // console.log(response.data.awardCount)
        this.cishu = response.data.awardCount
      })
      .catch(error => {
        alert(error)
      })

    axios.get('http://shine.tunnel.qydev.com/user/luckyDraw')
      .then(response => {
        console.log(response)
        this.jiangpins = response.data
      })
      .catch(error => {
        alert(error)
      })

      this.openId = this.$store.state.openId
  },
  mounted() {
    // 
    let that = this
    //
    //
    let zhizhen = document.getElementById("zhizhen");
    //id
    let dbox = null;
    //
    let dtime = 1;
    //css,0
    let deg = null;
    //
    let sdeg = 0;
    //js
    zhizhen.style.transform = "rotate(" + sdeg + "deg)";
    //
    let cc = 5;
    //
    let quan = 6;
    //
    let odeg = null;
    //
    let stopdeg = null;
    let alltime = 0;
    //
    let jiang = [
      [1, 51, 0], //
      [52, 102, 6], //6
      [103, 153, 5], //5
      [154, 203, 4], //4
      [204, 251, 3], //3
      [252, 307, 2], //2
      [307, 360, 1] //1
    ];
    //
    // let cishu = this.cishu;
    //
    function is(deg) {
      let res = "";
      for (let i = 0; i < jiang.length; i++) {
        if (deg >= jiang[i][0] && deg <= jiang[i][1]) {
          res = jiang[i][2];
        };
      };
      return res;
    };
    //
    let able = false;
    //
    let gailv = [
      [838, 0],
      [118, 6],
      [14, 5],
      [12, 4],
      [8, 3],
      [6, 2],
      [4, 1]
    ];
    //
    let xq = 0;
    //
    function set(real) {
      let mindeg, maxdeg;
      for (let i = 0; i < jiang.length; i++) {
        if (real == jiang[i][2]) {
          mindeg = jiang[i][0];
          maxdeg = jiang[i][1];
        };
      };
      return mindeg + Math.floor(Math.random() * (maxdeg - mindeg + 1)); //min-max
    };
    //
    zhizhen.onclick = function() {
      // that.test()
      // console.log(that.openId)
      if (!able) {
        if (that.cishu == 0) { //
          // alert("");
          Toast({
            message: '^^'
          })
        } else {
          // this.cishu -= 1; //
          that.jian()
          deg = sdeg;
          cc = 5;
          xq = 0;
          let allarr = []; //10000-6 
          for (let i = 0; i < gailv.length; i++) {
            for (let j = 0; j < gailv[i][0]; j++) {
              allarr.push(gailv[i][1]);
            };
          };
          let real = allarr[Math.floor(Math.random() * 1000)];
          odeg = set(real);
          stopdeg = quan * 360 + odeg;
          alltime = stopdeg / cc * dtime;
          dbox = setInterval(dong, dtime);
        };
      };

    };
    //
    function dong() {
      able = true;
      deg += cc;
      if (deg > stopdeg) {
        clearInterval(dbox);
        setTimeout(function() {
          able = false;
          // alert(is(odeg));
          if (is(odeg) == 0) {
            // alert(is(odeg))
            axios.post('http://shine.tunnel.qydev.com/user/luckyDraw', {
                openId: that.openId,
                prizeId: 0,
                status: 0,
              })
              .then((response) => {
                Toast({
                  message: ``
                })
              })
              .catch(error => {
                // console.log(this.$store.state.openId)
                alert(error)
              })
          } else {
            // alert(is(odeg))
            // console.log(this.$store.state.openId)
            that.aword = is(odeg)
            axios.post('http://shine.tunnel.qydev.com/user/luckyDraw', {
                openId: that.openId,
                prizeId: that.aword,
                status: 1,
              })
              .then(() => {
                Toast({
                  message: `${that.aword},`
                })
              })
              .catch(error => {
                // console.log(this.$store.state.openId)
                alert(error)
              })
          }
        }, 500)
      } else {
        if (deg % 360 == 0) { //
          xq += 1;
          if (xq == quan - 1) { //
            cc = 1; //1 
          };
          zhizhen.style.transform = "rotate(" + deg + "deg)";
        } else {
          zhizhen.style.transform = "rotate(" + deg + "deg)";
        };
      };
    }
  },
  methods: {
    // -1
    jian() {
      this.cishu--
    }
  },
  store
}

</script>
<style type="text/css" scoped>
/*demo*/

.cishu {
  margin: 6% 10%;
}

#box {
  width: 300px;
  height: 300px;
  margin: 0 auto;
  position: relative;
  background: url(http://123.206.218.163/picture/back.png) no-repeat left top;
}

#zhizhen {
  width: 80px;
  height: 104px;
  position: absolute;
  background: url(http://123.206.218.163/picture/pointer.png) no-repeat left top;
  left: 110px;
  z-index: 999;
  cursor: pointer;
  top: 86px;
  transform-origin: 40px 64px;
}

.jiangpin ul {
  list-style: none;
  margin: 5% 0 5% 30%;
}

.jiangpin li {
  /*float: left;*/
  /*margin: 1% 0 0 0;*/
  font-size: .8em;
}

.history {
  margin: 3% 0 5% 60%;
}

</style>



