<template>
  <div>
    <!-- <div class="block"> </div> -->
    <p class="signupTips"><strong></strong></p>
    <div class="form">
      <mt-field label="" placeholder="" v-model="name"></mt-field>
      <mt-field label="" placeholder="" type="number" v-model="id_card"></mt-field>
      <mt-field label="" placeholder="" type="number" v-model="phoneNo"></mt-field>
    </div>
    <!-- {{this.username}} - {{ this.shenfenzheng }} - {{ this.phone }} -->
    <mt-button class="post" type="primary" size="small" @click.native="post"></mt-button>
  </div>
</template>
<script type="text/javascript">
import { Field } from 'mint-ui'
import axios from 'axios'

export default {
  name: 'signup',
  data() {
    return {
      name: '',
      id_card: '',
      phoneNo: '',
      openId: '',
    }
  },
  methods: {
    post() {
      if (this.name == '') {
        alert('')
      } else if (this.shenfengzheng == '') {
        alert('')
      } else if (this.phoneNo == '') {
        alert('')
      } else {
        var url = location.href; //url"?"
        var strs = []
        if (url.indexOf("?") != -1) { //
          var str = url.substr(1) // 0? 
          strs = str.split("=") //    & 
          // openid
          // console.log(strs[1])
          // alert(strs[1]); //  
          this.openId = strs[1]
        }
        let Data = {
          'name': this.name,
          'idCard': this.id_card,
          'phoneNo': this.phoneNo,
          'openId': this.openId
        }
        // console.log(Data)
        axios.post('http://shine.tunnel.qydev.com/user/member', Data)
          .then(response => {
            window.location = "http://shine.tunnel.qydev.com/wechat/authorize?returnUrl=http://shine.tunnel.qydev.com/"
          })
          .catch(error => {
            alert(error)
            // window.location = "http://shine.tunnel.qydev.com/authorize?returnUrl=http://shine.tunnel.qydev.com/"
          })
      }
    }
  }
}

</script>
<style type="text/css" scoped>
.signupTips {
  font-size: 5px;
  margin: 10%;
}

.form {
  margin-top: 50px;
}

.post {
  margin: 50px 0 0 200px;
}

</style>



